@font-face {
  font-family: "volt";
  src: url("/static/fonts/Voltage Bold.otf"); }

@font-face {
  font-family: "raleway-extrabold";
  src: url("/static/fonts/Raleway-ExtraBold.ttf"); }

@font-face {
  font-family: "raleway-light";
  src: url("/static/fonts/Raleway-Light.ttf"); }

@font-face {
  font-family: "raleway-regular";
  src: url("/static/fonts/Raleway-Regular.ttf"); }

.footer {
  position: absolute;
  bottom: 0;
  padding: 50px;
  width: 100%;
  background-size: cover;
  background: #192e55; }
  .footer .footer_container {
    max-width: 1200px;
    margin: auto;
    display: flex; }
    .footer .footer_container .site_map {
      display: flex;
      font-family: "raleway-light";
      color: white;
      font-size: 12px;
      line-height: 2;
      letter-spacing: 1px; }
      .footer .footer_container .site_map .site_map_column {
        padding-right: 5%; }
        .footer .footer_container .site_map .site_map_column .bold {
          font-family: "raleway-extrabold"; }
    .footer .footer_container .footer_connect {
      color: white; }
      .footer .footer_container .footer_connect h3 {
        font-family: "volt", sans-serif;
        font-size: 32px; }
      .footer .footer_container .footer_connect .news_letter .signup_form {
        display: flex; }
        .footer .footer_container .footer_connect .news_letter .signup_form input {
          font-family: "raleway-regular";
          font-size: 16px;
          width: 300px;
          padding: 20px 15px;
          border-style: none;
          border: none;
          height: 56px; }
        .footer .footer_container .footer_connect .news_letter .signup_form button {
          font-family: "raleway-extrabold";
          font-size: 16px;
          width: 100px;
          height: 56px;
          border-style: none;
          background: #76b3a8;
          color: white; }
        .footer .footer_container .footer_connect .news_letter .signup_form span {
          border-top: 28px solid #76b3a8;
          border-bottom: 28px solid #76b3a8;
          border-right: 10px solid transparent;
          height: 0;
          width: 0; }
      .footer .footer_container .footer_connect .connect {
        display: flex; }
        .footer .footer_container .footer_connect .connect p {
          font-family: "raleway-regular";
          font-size: 16px;
          line-height: 1.5; }
